import { Vector } from '../vector';
/** @ignore */
export class Run {
  get length() {
    return this._values.length;
  }
  get(index) {
    return this._values[index];
  }
  clear() {
    this._values = null;
    return this;
  }
  bind(values) {
    if (values instanceof Vector) {
      return values;
    }
    this._values = values;
    return this;
  }
}